.nav {
  background-color: blueviolet;
  color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.nav a {
  text-align: left;
  text-decoration: none;
}

.nav ul {
  list-style: none;
  display: flex;
  gap: 1rem;
}

.site-title {
  font-size: 2rem;
  padding: 10px;
}

.nav li {
    margin: .25rem;
    padding: .5rem;
}

.nav li:hover {
  background-color: red;
  border-radius: 5px;
  transform: scale(1.2);
  transition: all 0.2s ease;
}

.nav li:not(:hover) {
    margin: .25rem;
    padding: .5rem;
    transform: scale(1);
    transition: all 0.3s ease;
}

.site-title:hover {
    background-color: red;
    border-radius: 5px;
    transform: scale(1);
    transition: all 0.2s ease;
  }
  
.site-title:not(:hover) {
      transform: scale(1);
      transition: all 0.3s ease;
}
